import React, { useState } from "react";
import { Modal, Form, Input, message } from "antd";
import "./Hero.css";

const Hero = () => {
  const [showTooltip, setShowTooltip] = useState(false);
  const [isModalOpen, setIsModalOpen] = useState(false);

  const [form] = Form.useForm();

  const handleSubmit = (values) => {
    console.log(" :", values);
    message.success(" !");
    form.resetFields();
    setIsModalOpen(false);
  };

  return (
    <section className="hero" id="hero">
      <div className="hero-overlay"></div>
      <div className="hero-content">
        <h1 className="hero-title">
          <div className="hero-title-wrapper">
             
            <span className="hero-plus-wrapper">
              <span
                className="hero-plus"
                onClick={() => setShowTooltip((prev) => !prev)}
                onMouseEnter={() =>
                  window.innerWidth > 768 && setShowTooltip(true)
                }
                onMouseLeave={() =>
                  window.innerWidth > 768 && setShowTooltip(false)
                }
              >
                +
              </span>
              {showTooltip && (
                <div className="hero-tooltip">
                  <ul>
                    <li>    2020 </li>
                    <li>    3 </li>
                    <li>     20  </li>
                    <li>     </li>
                    <li>
                           -
                    </li>
                  </ul>
                </div>
              )}
            </span>
          </div>
        </h1>
        <p className="hero-description">
                
        </p>
        <button className="hero-button" onClick={() => setIsModalOpen(true)}>
          
        </button>
      </div>

      <Modal
        open={isModalOpen}
        footer={null}
        onCancel={() => setIsModalOpen(false)}
        centered
        className="custom-modal"
      >
        <div className="modal-content-wrapper">
          <img
            src={`${process.env.PUBLIC_URL}/images/img1.jpg`}
            alt=" "
            className="modal-photo"
          />
          <div className="modal-form">
            <h2 className="modal-title">  </h2>
            <p className="modal-subtext">
                        
            </p>

            <Form layout="vertical" form={form} onFinish={handleSubmit}>
              <Form.Item
                name="name"
                rules={[{ required: true, message: " " }]}
              >
                <Input placeholder="  ?" />
              </Form.Item>
              <Form.Item
                name="email"
                rules={[
                  { required: true, message: " email" },
                  { type: "email", message: "  email" },
                ]}
              >
                <Input placeholder=" email" />
              </Form.Item>
              <Form.Item>
                <button type="submit" className="submit-button">
                  
                </button>
              </Form.Item>
            </Form>
          </div>
        </div>
      </Modal>
    </section>
  );
};

export default Hero;
