import React, { useEffect, useState } from "react";
import { Card, Avatar, Modal } from "antd";
import "./Reviews.css";

const reviewsData = [
  {
    name: " ",
    position: " , ",
    avatarUrl:
      "https://i.pinimg.com/736x/1c/e7/6b/1ce76ba4340f67dcd59b5b5b16249f5b.jpg",
    review: `<p>               .       ,      ,   .</p> 
    <p>          ,     .     ,           .       ,         .</p> 
    <p>         .    ,      ,    !       !`,
  },
  {
    name: " ",
    position: ", -",
    avatarUrl:
      "https://i.pinimg.com/736x/c1/03/4f/c1034f45ca068b532f8fbdc7b3f57642.jpg",
    review: `<p>       !               .     ,        .          !</p>`,
  },
  {
    name: " ",
    position: " , ",
    avatarUrl:
      "https://i.pinimg.com/736x/01/c9/47/01c9475adeacc260995b4f9281675b0d.jpg",
    review: `<p>           !       ,       ,   .</p>
      <p>          ,      .     ,    ,           . </p>
      <p>               .      ,      ,      ,   !</p>`,
  },
];

const Reviews = () => {
  const [isVisible, setIsVisible] = useState(false);

  const [modalVisible, setModalVisible] = useState(false);
  const [modalContent, setModalContent] = useState(null);

  const openModal = (review) => {
    setModalContent(review);
    setModalVisible(true);
  };

  const closeModal = () => {
    setModalVisible(false);
    setModalContent(null);
  };

  useEffect(() => {
    const handleScroll = () => {
      const section = document.getElementById("reviews");
      if (section) {
        const rect = section.getBoundingClientRect();
        if (rect.top < window.innerHeight * 0.75) {
          setIsVisible(true);
        }
      }
    };

    window.addEventListener("scroll", handleScroll);
    handleScroll();
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <section
      className={`reviews ${isVisible ? "visible" : "hidden"}`}
      id="reviews"
    >
      <h2></h2>
      <div className="reviews-container">
        {reviewsData.map((review, index) => (
          <Card key={index} className="review-card">
            <div className="review-header">
              <Avatar src={review.avatarUrl} size={64} />
              <div>
                <h3>{review.name}</h3>
                <p>{review.position}</p>
              </div>
            </div>
            <div
              className="review-text"
              dangerouslySetInnerHTML={{
                __html:
                  review.review.split("</p>")[0] +
                  "</p><p><strong>,   ...</strong></p>",
              }}
              onClick={() => openModal(review)}
              style={{ cursor: "pointer" }}
            ></div>
            <Modal
              title={modalContent?.name}
              open={modalVisible}
              onCancel={closeModal}
              footer={null}
            >
              <p>
                <strong>{modalContent?.position}</strong>
              </p>
              <div
                dangerouslySetInnerHTML={{ __html: modalContent?.review }}
                className="review-text"
              />
            </Modal>
          </Card>
        ))}
      </div>
    </section>
  );
};

export default Reviews;
